.game-set-container {
    display: flex; /* ใช้ Flexbox */
    flex-direction: column; /* จัดเรียงเนื้อหาในแนวตั้ง */
    align-items: center; /* จัดให้อยู่กึ่งกลางในแนวนอน */
    justify-content: center; /* จัดให้อยู่กึ่งกลางในแนวตั้ง */
    width: 100%; /* ครอบคลุมพื้นที่หน้าจอ */
    height: 100vh; /* ใช้ความสูงเต็มหน้าจอ */
    background-color: #f9f9f9; /* สีพื้นหลัง */
    padding: 20px;
    box-sizing: border-box; /* รวม padding ใน width และ height */
  }
  
  h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .g-error-message {
    color: red;
    text-align: center;
  }
  
  .g-success-message {
    color: green;
    text-align: center;
  }
  
  .g-user-selection, .g-balance-update, .g-win-rate-update {
    margin-bottom: 20px;
    width: 100%; /* ทำให้ฟอร์มเต็มความกว้าง */
    max-width: 300px; /* กำหนดความกว้างสูงสุด */
  }
  
  .g-user-selection label, .balance-update h2, .win-rate-update h2 {
    display: block;
    margin-bottom: 10px;
  }
  
  input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .g-button {
    width: 100%; /* ทำให้ปุ่มกว้างเต็มฟอร์ม */
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .g-button:hover {
    background-color: #0056b3;
  }
  
  .g-user-details {
    text-align: center; /* จัดให้อยู่กึ่งกลาง */
    margin-bottom: 20px;
  }
  